import ReactDOM from "react-dom";
import React, { Component } from "react";
import { BrowserRouter as Router, Route} from "react-router-dom";
import App from "../app.js";
import Other from "../page/block/comp/stupid_component/other/other.js";
import Loadable from "../page/block/Loadable/Load.js";

/*  */
const my_BuferSTANDART = JSON.stringify({
  news: { id_store: {}, st: [], len: 10, my_var: 10 },
  howThisWorks: { id_store: {}, st: [], len: 10, my_var: 10 },
  homePageDefault: { id_store: {}, st: [], len: 10, my_var: 10 }
});
let language = window.navigator.language,
  my_Bufer ={my_BuferSTANDART, buf:JSON.parse(my_BuferSTANDART)},
  lang,
  my_stor = window.localStorage.getItem("userLang_ae_fin_obj"),
  my_config = {
    url_cms: "https://cms.fin.so",
    name_page: {
      Header: true,
      footer: true,
      Article: true,
      Contacts: true,
      Cooperation: true,
      DirectForm: true,
      faq: true,
      faqAnswer: true,
      Fees: true,
      HomePageDefault: true,
      howThisWorks: true,
      htmltojsx: true,
      News: true,
      Payment: true,
      Reviews: true,
      SignIn: true,
      SignUp: true
    },
    localStorage: false
  };
if (my_stor) {
  // console.log(storge,"localStorage");
  try {
    my_stor = JSON.parse(my_stor);
    if (
      my_stor&&
     my_stor.lang &&
     my_stor.page &&
      typeof my_stor.page === "object" &&
     my_stor.lang_array &&
      Array.isArray(my_stor.lang_array)
    ) {
      console.log("STOR");
      my_config.localStorage = true;
    } else {
      f_lang();
    }
    //   console.log(stor,"localStorage");
    my_config.localStorage = true;
  } catch (error) {
    console.error(error);
    f_lang();
  }
} else {
  // console.log("NoDE localStorage");
  f_lang();
}
function f_lang() {
  console.log("no_STOR");
  if (/ru/gim.test(language)) {
    lang = "ru";
  } else {
    lang = "en";
  }
  my_stor = {
    lang,
    page: {},
    lang_array: ["en", "ru", "cn"]
  };
  (Array.isArray(my_stor.lang_array) ?my_stor.lang_array : []).map((a, b) => {
   my_stor.page[a] = {};
    (Array.isArray(Object.keys(my_config.name_page))
      ? Object.keys(my_config.name_page)
      : []
    ).map((a_1, b_1) => {
     my_stor.page[a][a_1] = {};
      for (let i = 0; i < 100; i++) {
       my_stor.page[a][a_1]["r" + i] = "";
      }
      return "";
    });
    return "";
  });
}
function my_url(url) {
  return "" + url;
}
function my_render(data) {
  console.log("RENDER");

  class Root extends Component {
    render() {
      return (
        <Router>
          <Route exact path="/*" component={App} />
        </Router>
      );
    }
  }
  ReactDOM.render(<Root />, document.getElementById("root"));
}
// console.log(url, stor, config, render, my_Bufer);

class Faq_app extends Component {
  render() {
    const url = this.props.match.url.split("/");  
    if (url && Loadable[url[url.length - 1]]) {
      let Lob=Loadable[url[url.length - 1]];
        return <Lob
         page_name="faq"
          data={url[url.length - 1]} 
          language={my_stor.page[my_stor.lang]}
          />;
    } else {
      return <Other url={url}/>;
    }
  }
}
export { Faq_app };
export { my_stor };
export { my_url };
export { my_Bufer };
export { my_config };
export { my_render };
export { my_BuferSTANDART };


